import React from 'react';
import PropTypes from 'prop-types';
import { getDetailedWeather } from '../util/Api';
import WeatherIconSelector from '../components/WeatherIconSelector';
import './CityItem.css';


function formatDate(date) {
  const formatedDate = new Date(date);
  const time = `${formatedDate.getHours()}`.length === 1 ? `0${formatedDate.getHours()}:00` : `${formatedDate.getHours()}:00`;
  return `${formatedDate.getMonth() + 1} / ${formatedDate.getDate()} ${time}`;
}

class CityItem extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      city: { name: '' },
      weatherList: [],
    };
  }

  componentDidMount() {
    getDetailedWeather(this.props.match.params.id)
      .then(res => this.setState({ city: res.city, weatherList: res.list }))
      .catch(error => console.error('error', error));
  }


  render() {
    return (
      <div className="city-item">
        {this.state.city.name}
        <div className="weather-list">
          {this.state.weatherList.map(item => (
            <div key={item.dt} className="weather-list-item">
              <div className="weather-list-date">
                {formatDate(item.dt_txt)}
              </div>
              <div className="weather-item">
                <div>
                  {Math.round(item.main.temp)}
                </div>
                <div>
                  <WeatherIconSelector type={item.weather[0].main} />
                </div>
              </div>
            </div>))}
        </div>
      </div>
    );
  }
}

CityItem.defaultProps = {
  match: null,
};

CityItem.propTypes = {
  match: PropTypes.shape({ params: PropTypes.shape({ id: PropTypes.string.isRequired }) }),
};

export default CityItem;



